import React, { Component } from "react";
import { Link } from "react-router-dom";
import validate from "../../../validators/validate";
import TextInput from "../../../Form/TextInput";
import PasswordInput from "../../../Form/PasswordInput";
// import HttpService from "../../../services/HttpService";
import { login } from "../../../services/HttpService"

import {
  Button,
  Card,
  CardBody,
  CardGroup,
  Col,
  Container,
  Form,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Row
} from "reactstrap";
// import { userInfo } from "os";

class Login extends Component {
  constructor() {
    super();
    this.state = {
      isDanger : false,
      isSuccess:  false,
      message : "",
      formIsValid: false,
      formControls: {
        email: {
          value: "",
          placeholder: "Email or Username",
          valid: false,
          touched: false,
          validationRules: {
            isEmail: true
          }
        },
        password: {
          value: "",
          placeholder: "Password",
          valid: false,
          touched: false,
          validationRules: {
            isRequired: true
          }
        }
      }
    };
  }

  /**
   * input field change handler
   */
  changeHandler = event => {
    const name = event.target.name;
    const value = event.target.value;
    const updatedControls = {
      ...this.state.formControls
    };
    const updatedFormElement = {
      ...updatedControls[name]
    };
    updatedFormElement.value = value;
    updatedFormElement.touched = true;
    updatedFormElement.valid = validate(
      value,
      updatedFormElement.validationRules
    );

    updatedControls[name] = updatedFormElement;

    let formIsValid = true;
    for (let inputIdentifier in updatedControls) {
      formIsValid = updatedControls[inputIdentifier].valid && formIsValid;
    }
    this.setState({
      formIsValid: formIsValid,
      formControls: updatedControls
    });
  };

  /**
   * submit button login
   */
  submitLoginForm = async event => {
    event.preventDefault();
    const formData = {};
    for (let formElementId in this.state.formControls) {
      formData[formElementId] = this.state.formControls[formElementId].value;
    }

    // email and password
    try{
      let res = await login(formData)
      sessionStorage.setItem('userId' , res[1]._id)
      console.log(res)
      this.setState({
        isDanger : false,
        isSuccess : true,
        message : res[0].Message
      })
      this.props.history.push('/dashboard')
    } catch (e){
      this.setState({
        isDanger : true,
        isSuccess : false,
        message : e[0].Message
      })
    }
  };

  /**
   * display login message after api call, wether it is success or fail
  */
  loginMessage () {
    if (this.state.isDanger) {
      return (
        <div className="alert alert-danger" role="alert">
          {this.state.message}
        </div>
      )
    }
    else if (this.state.isSuccess) {
      return (
        <div className="alert alert-success" role="alert">
          {this.state.message}
        </div>
      )
    }
    else return
  }

  render() {
      return (
        <div className="app flex-row align-items-center">
          <Container>
            <Row className="justify-content-center">
              <Col md="8">
                <CardGroup>
                  <Card className="p-4">
                    <CardBody>
                      {this.loginMessage()}
                      <Form onSubmit={this.submitLoginForm}>
                        <h1>Login</h1>
                        <p className="text-muted">Sign In to your account</p>
                        <div className="form-group">
                          <InputGroup className="mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="icon-user" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <TextInput
                              name="email"
                              placeholder={this.state.formControls.email.placeholder}
                              value={this.state.formControls.email.value}
                              onChange={this.changeHandler}
                              touched={this.state.formControls.email.touched ? 1 : 0}
                              valid={this.state.formControls.email.valid ? 1 : 0}
                            />
                          </InputGroup>
                        </div>
                        <div className="form-group">
                          <InputGroup className="mb-4">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                                <i className="icon-lock" />
                              </InputGroupText>
                            </InputGroupAddon>
                            <PasswordInput
                              name="password"
                              placeholder={this.state.formControls.password.placeholder}
                              value={this.state.formControls.password.value}
                              onChange={this.changeHandler}
                              touched={this.state.formControls.password.touched ? 1 : 0}
                              valid={this.state.formControls.password.valid ? 1 : 0}
                            />
                          </InputGroup>
                        </div>
                        <Row>
                          <Col xs="6">
                            <Button
                              color="primary"
                              className="px-4"
                              type="submit"
                              disabled={!this.state.formIsValid}
                              style={{cursor:"pointer"}}
                            >
                              Login
                            </Button>
                          </Col>
                          {/* <Col xs="6" className="text-right">
                            <Button color="link" className="px-0">
                              Forgot password?
                            </Button>
                          </Col> */}
                        </Row>
                      </Form>
                    </CardBody>
                  </Card>
                  <Card
                    className="text-white bg-primary py-5 d-md-down-none"
                    style={{ width: "44%" }}
                  >
                    <CardBody className="text-center">
                      <div>
                        <h2>Sign up</h2>
                        <p>
                          Create new user account.
                        </p>
                        <Link to="/register" style={{cursor:"pointer"}}>
                          <Button
                            color="primary"
                            className="mt-3"
                            active
                            tabIndex={-1}
                          >
                            Register Now!
                          </Button>
                        </Link>
                      </div>
                    </CardBody>
                  </Card>
                </CardGroup>
              </Col>
            </Row>
          </Container>
        </div>
      );
  }
}

export default Login;
